@charset "UTF-8";
@import 'mixin';

/*Detail*/
.App {
  text-align: center;
  margin: 0 auto;
  /* overflow: hidden; */
}

/*header*/
.navbar {
  backdrop-filter: blur(10px);
  padding: 20px 0;

  @include semi {
    flex-wrap: nowrap;

  }

  .head container {
    backdrop-filter: blur(90px) !important;
  }

}

.only {
  height: 78px;
  width: 100%;
  z-index: 100;
  --bs-bg-opacity: 0;
}

.change {
  position: fixed;
  color: black;
  background-color: black !important;
  width: 100%;
  height: 80px;
  top: 0;

  .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba(255, 255, 255, 0.5)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
  }

  .navbar-toggler {
    border-color: rgb(255, 255, 255);
  }

  .head {
    background-color: black;


    .navbar-toggler-icon {
      color: white;
    }
  }

}

.logo {
  height: 78px;
  width: 200px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 3;
  cursor: pointer;

  @include semi {
    // display: none;
    position: static;
    margin: 0;
  }
}

.logo img {
  width: 100%;
  height: 100%;
}

.container {
  max-width: 100%;
  font-size: 1.5rem;
  font-weight: 800;
}

.me-auto {
  font-weight: bold;

  @include semi {
    font-size: 1.2rem;
    white-space: nowrap;
  }
}

.ICon {
  padding-top: 10px;
  padding-right: 5px;
  @include semi {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.me-auto .nav-link {
  color: white;
  padding-top: 20px;
}

.me-auto .nav-link:nth-child(5){
  @include Mta {
    display: none;
  }
}
.me-auto .nav-link:nth-child(6){
  @include Mta {
    display: none;
  }
}

.navbar-collapse {
  position: absolute;
  top: 0px;
  left: 0;
  background-color: transparent;
  width: 100%;

  @include semi {
    top: 78px;
    background-color: rgba(0, 0, 0, 0.5);
  }
}

/*footer*/
footer {
  width: 100%;
  height: auto;
  color: white;
  background: rgb(0, 0, 0);
}

.footer_cover {
  max-width: 1692px;
  margin: 0 auto;
  height: auto;
}

.bottom_cover {
  height: 200px;
  border-bottom: 2px solid gray;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bottom_cover .imgbox {
  width: 320px;
  height: 78%;
}

.col-xl-3 {
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.index {
  height: auto;
  margin: 1%;
  padding-top: 20px;
}

.index h2 {
  font-size: 1.7rem;
  font-weight: bold;
}

.index p {
  font-size: 1.2rem;
  margin-top: 20px;
}

.index .p_name {
  font-size: 1.1rem;
  cursor: pointer;
}

.index .p_name span:hover {
  font-weight: bold;
}

.img_in {
  display: flex;
  align-items: center;
}

.img_in img {
  margin: 0 5% 0 2%;
}

.img_in p {
  margin-top: 20px;
}

.col-xxl-12 {
  height: 40px;
  font-size: 1rem;
}

.col-xxl-12 p {
  margin-top: 5px;
}

/*Scroll Top 고정*/
.scroll__Cover {
  position: fixed;
  right: 2%;
  bottom: 11%;
  z-index: 100;
  display: flex;
  flex-direction: column;
}

#top {
  font-weight: bold;
  font-size: 40px;
  /* padding: 10px 10px; */
  background-color: #db0b15;
  color: #fff;
  border: 2px solid rgb(210, 204, 193);
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  width: 70px;
  height: 70px;
  margin-top: 20px;
}

#top:hover {
  /* color: rgb(142, 26, 26); */
  background: rgba(61, 61, 61, 0.9);
}