@charset "utf-8";
@import 'mixin';

.Cart_Container{
    margin-top: 10%;
    @include Mta{
        margin-top: 20%;
        width: 100%;
        }
    h1{
        font-weight: bold;
    }
}
/*카트 테이블*/
.Cart_table{
    margin-top: 5%;
    @include Mta{
        white-space: nowrap;
        width: 100%;
    }
    tbody{
        font-size: 1.2rem;
        line-height: 200px;
        @include Mta{
            white-space: nowrap;
            font-size: 1rem;
            }
        // position: relative;
        .form-check-input{
            position: relative;
            top: 90px;
            // left: 75px;
        }    
    }
    th{
            font-size: 1.2rem;
            @include Mta{
            // white-space: nowrap;
            font-size: 1rem;
            // width: 100%;
            }
        }
    .bo{
        font-weight: bold;
    }
}
/*총 금액*/
.Total{
    width: 90%;
    margin: 0 auto;
    margin-top: 3%;
    // padding-top: 10%;
    // width: 1300px;
    tr{
        // height: 50px;
        border: 1px solid black;
    }
    th{
        height: 70px;
        border: 2px solid rgb(201, 201, 201);
        font-size: 1.3rem;
        line-height: 50px;
    }
    td{
        height: 100px;
        border: 2px solid rgb(201, 201, 201);
        font-size: 1.5rem;
        line-height: 90px;
        font-weight: bold
    }
}
/*수량 버튼 커스텀*/
.p_m_btn{
    @include sub_Color;
    border: none;
    color: white;
    font-weight: bold;
    width: 25px;
    height: 25px;
    margin: 5%;
    line-height: 10px;
}
.form-check-input[type=checkbox]{
    border: 2px solid #db0b15;
    width: 20px;
    height: 20px;
}
.form-check-input:checked{
    background-color: #db0b15;
}
.Delete{
    @include sub_Color;
}
.bt_btn{
    background: #db0b15;
    height: 60px;
    width: 200px;
    border: none;
    color: white;
    font-size: 1.3rem; 
}
.bt_btn:hover{
        opacity: 0.7;
    }
