@charset "UTF-8";
@import 'mixin';


/*header*/
.onlyone {
  height: 78px;
  width: 100%;
  z-index: 100;
  background: #db0b15;
}

/*Detail*/
.Detail_Container {
  max-width: 1692px;
  margin: 0 auto;
}

.left_box {
  height: 850px;
  position: sticky;
  top: 100px;
  @include Mta{
    position: relative;
    margin-bottom: 150px;
    width: 100%;
    height: auto;
    // background: palegoldenrod;
  }
}
.heade {
  padding-top: 5%;
  width: 100%;
}

.heade .imgbox {
  width: 100%;
}
//구매하기 버튼
.Go-btn:hover{
  opacity: 0.6;
}
/*상품 메인 틀 박스*/
.content_box {
  height: auto;
  margin-top: 3%;
}

.cover {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: auto;
  background: plum;
}

.Fixed_box {
  background: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.3) 10px 10px 10px 10px;
  width: 50%;
  height: 90px;
  position: sticky;
  bottom: 0;
  float: right;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include Mta{
    width: 100%;
  }
}

.Fixed_box .t_box {
  display: flex;
  margin: 5%;
  padding-top: 10px;
  @include Mta{
    margin: 3%;
    margin-top: 20px;
  }
}

.Fixed_box .t_box h5 {
  padding-right: 30px;
  line-height: 40px;
  @include Mta{
    @include md;
  }
}

.Fixed_box .t_box h2 {
  font-weight: bold;
  @include mobile{
    @include md;
    margin-top: 10px;
  }
}

.Fixed_box .b_box {
  display: flex;
  margin-right: 12%;
  @include Mta{
    margin-right: 2%;
  }
}

.Fixed_box .b_box button {
  width: 200px;
  border: none;
  background: #db0b15;
  color: white;
  height: 50px;
  margin-right: 7%;
  font-size: 1.3rem;
  @include mobile{
    width: 150px;
  }
}

.Fixed_box .b_box .im {
  background: rgb(0, 0, 0);
  width: 50px;
  height: 50px;
  padding: 3%;
  cursor: pointer;
}

.im:hover {
  opacity: 0.7;
}
.nav{
  @include Mta{
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.nav .ka_btn {
  width: auto;
  padding: 0 35px 0 35px;
  margin: 1.5%;
  height: 50px;
  border-radius: 50px;
  font-weight: bold;
  font-size: 1.2rem;
  color: black;
  @include Mta{
    margin-bottom: 20px;
  }
}

.ka_btn:hover {
  animation: shake 0.5s infinite;
}

@keyframes shake {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(-8deg);
  }

  50% {
    transform: rotate(8deg);
  }

  75% {
    transform: rotate(-8deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.kata {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  margin-top: 5%;
}

.kata p {
  font-weight: bold;
  color: rgb(71, 71, 71);
  @include Mta{
    font-size: 1.2rem;
  }
}

.kata .box:nth-of-type(1) {
  width: 70%;
  height: auto;
  text-align: left;
  font-size: 1.3rem;
}

.kata .box:nth-of-type(2) {
  width: 30%;
  height: auto;
  text-align: right;
  font-size: 1.3rem;
}

.kata img {
  margin: 4%;
}

.txtxt_box {
  height: 300px;
  text-align: left;
  @include Mta{
    font-size: 1.4rem;
  }
}

.txtxt_box h4 {
  font-size: 2rem;
  font-weight: bold;
  @include Mta{
    font-size: 1.6rem;
  }
}

.txtxt_box .dis {
  display: flex;
  margin-top: 7%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.txtxt_box .dis p {
  text-decoration: line-through;
  color: gray;
  width: 20%;
  padding-top: 20px;
  @include Mta{
    width: 30%;
  }
}

.txtxt_box .dis b {
  font-size: 2rem;
  width: 60%;
  margin-right: 15%;
}

.txtxt_box .dis span {
  font-weight: bold;
  color: red;
  width: 20%;
  text-align: right;
  padding-right: 10px;
  font-size: 1.6rem;
  padding-top: 20px;
}

.option {
  display: flex;
  height: 50px;
  margin-bottom: 5%;
}

.option .form-select {
  width: 80%;
  margin-left: 10%;
}

.option h3 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-top: 10px;
  @include Mta{
    font-size: 1.4rem;
  }
}

.Der {
  display: flex;
  height: 200px;
  margin-bottom: 10%;
  margin-top: 80px;
  border-bottom: 2px solid gray;
}

.Der h3 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-top: 10px;
  margin-right: 8%;
  @include Mta{
    font-size: 1.4rem;
  }
}

.Der p {
  text-align: left;
  @include Mta{
    font-size: 1.4rem;
    margin-top: 5px;
  }
}

/*Login*/
.login_container {
  margin: 0 auto;
  height: auto;
  margin-top: 10%;
  @include Margin_Bt;
  @include Mta{
    margin-top: 100px;
    }
  
  h2{
    font-size: 2.6rem;
    font-weight: bold;
    line-height: 90px;
  }
  .Ka_Txt{
    line-height: 50px;
  }
  p{
    font-size: 1rem;
    color: rgb(109, 109, 109);
  }
  .kakao {
    width: 400px;
    height: 50px;
    border: none;
    background: #ffeb00;
    font-size: 1.2rem;
  }
  .Coupon {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      margin: 5%;
      padding: 7%;
    }

    .Copon_Grop {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 1.2rem;
      span{
        font-size: 1.1rem;
      }
    }
  }
}
.kakao:hover{
  opacity: 0.7;
}
.Form_tull{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .form-control{
    height: 50px;
  }
  @include Mta{
    white-space: nowrap;
    }
}
.Login_btn{
  width: 32%;
  border: none;
  height: 50px;
  @include sub_Color;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
  @include Mta{
    width: 50%;
    }
}
.Login_btn:hover{
  opacity: 0.7;
}
.naver{
  width: 32%;
  border: none;
  height: 50px;
  background: #26c826;
  color: white;
  font-weight: bold;
  margin-top: 2%;
  font-size: 1.2rem;
  @include Mta{
    width: 50%;
    }
}
.naver:hover{
  opacity: 0.7;
}
.nav_txt{
  margin-top: 70px;
}
.memp{
  width: 32%;
  border: none;
  height: 50px;
  background: #000000;
  color: white;
  font-weight: bold;
  // margin-top: 2%;
  font-size: 1.2rem;
  @include Mta{
    width: 50%;
    }
}
.none_user{
  width: 32%;
  border: none;
  height: 50px;
  border: 2px solid black;
  font-weight: bold;
  margin-top: 2%;
  font-size: 1.2rem;
  @include Mta{
    width: 50%;
    }
}
.memp:hover{
  opacity: 0.7;
}
.none_user:hover{
  opacity: 0.7;
}

