@charset "utf-8";
@import "./Variables";

//Font
@mixin md{
    font-size: 1.1rem;
}
//모바일
@mixin mobile {
    @media (min-width: #{$Breakpoint-mobile}) and (max-width: #{$Breakpoint-tablet - 1px}) {
      @content;
    }
  }
//테블릿
@mixin tablet {
    @media (min-width: #{$Breakpoint-tablet}) and (max-width: #{$Breakpoint-desktop - 1px}) {
      @content;
    }
  }
   //세미
   @mixin semi {
    @media (min-width: #{$Breakpoint-mobile}) and (max-width: #{$Breakpoint-semi - 1px}) {
      @content;
    }
  }   
//모바일에서 테블릿
@mixin Mta {
    @media (min-width: #{$Breakpoint-mobile}) and (max-width: #{$Breakpoint-desktop - 1px}) {
      @content;
    }
  }
//데스크탑
@mixin desktop {
    @media (min-width: #{$Breakpoint-desktop}) {
      @content;
    }
  }
  //미들
@mixin medle {
    @media (min-width: #{$Breakpoint-desktop}) and (max-width: #{$Breakpoint-medle - 1px}) {
        @content;
      }
  }
@mixin sub_Color{
    background: #db0b15;
}
@mixin sub_Txt_color{
    color: rgb(109, 109, 109);
}
@mixin Margin_Bt{
    margin-bottom: 15%;
}
@mixin sub_box{
    max-width: 1692px;
    margin: 0 auto;
}
@mixin Full_box{
    width: 100%;
    // background: palegoldenrod;
}
@mixin header_style{
    max-width: 100%;
    font-size: 1.5rem;
    font-weight: 800;
}
@mixin font_English{
    font-family: "Lilita One", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-weight: bold;
}
/*중제목*/
@mixin title_txt{
    font-size: 2.6rem;
    text-align: center;
    font-weight: bold;
}
/*소제목*/
@mixin title_sub_txt{
    font-size: 1.5rem;
    text-align: center;
}
/*상품 이름*/
@mixin product_main_txt{
    font-size: 1.5rem;
    text-align: center;
    font-weight: bold;
}
/*가격 등*/
@mixin product_sub_txt{
    font-size: 1.2rem;
    text-align: center;
}
/*카테고리*/
@mixin ka_txt{
    font-size: 2rem;
    text-align: center;
    font-weight: bold;
}
/*Event 중제목*/
@mixin Event_title{
    font-size: 2rem;
    font-weight: bold;
    padding-top: 10px;
}
/*Event 설명*/
@mixin Event_sub{
    font-size: 1.2rem;
    // line-height: 50px;
    padding-top: 15px;
    // font-weight: bold;
}
/*Detail*/
@mixin Product_title{
    font-size: 2rem;
    font-weight: bold;
}
@mixin Product_sub{
    font-size: 1.5rem;
    font-weight: bold;
}
