@charset "utf-8";
@import 'mixin';

.swiper {
    width: 100%;
    height: 100%;
    @include Mta{
        margin-bottom: 100px !important;
    }
}
// 클릭 슬라이드 커버
.Slide2_cover {
    text-align: center;
    font-size: 1.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25%;
    height: 100%;
    opacity: 0.4;
    transition: all 0.3s ease-out;
}
.Slide2_cover:hover{
    transform: translateY(-50px);
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper {
    width: 100%;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
}

.swiper-slide {
    background-size: cover;
    background-position: center;
}

.mySwiper_Slide {
    height: 100%;
    width: 100%;
    position: relative;
}

.mySwiper_Slide2 {
    height: 100%;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    width: 100%;
    cursor: pointer;
    @include mobile{
        display: none;
    }
    @include tablet{
        display: none;
    }
    @include medle{
        position: absolute;
    top: -200px;
    }
    //Text
    .Text_box{
        text-align: left;
        color: white;
        position: absolute;
        text-shadow: 4px 4px 4px black;
        bottom: 0;
        b{
            font-size: 2rem;
        }
    }
}

.mySwiper_Slide2 .swiper-slide-thumb-active {
    opacity: 1;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

