@charset "utf-8";
@import 'mixin';

/*Review*/
.Review_ontainer {
      height: auto;
      margin-bottom: 10%;
  }
  .Review_Cover h2 {
    text-align: left;
    font-size: 2.6rem;
    margin-bottom: 50px;
    font-weight: bold;
    @include Mta{
      font-size: 2.4rem;
    }
  }
  
  .horoscope {
    width: 100%;
    height: 300px;
    display: flex;
  }
  
  .horoscope_box {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 2%;
    @include Mta{
      margin: 3%;
    }
  }
  .horoscope_box h1 {
    font-weight: bold;
    margin-bottom: 5%;
  }
  .horoscope_box .btn1 {
    width: 200px;
    height: 50px;
    background: #db0b15;
    font-size: 1.2rem;
    font-weight: bold;
    @include Mta{
      font-size: 1rem;
    }
  }
  .horoscope_box p {
    font-size: 1.2rem;
    @include Mta{
      font-size: 1rem;
    }
  }
  .horoscope_box .good_box {
    width: 100%;
    height: 30px;
    margin-bottom: 10%;
    display: flex;
    justify-content: space-between;
    @include Mta{
      width: 110%;
    }
  }
  .horoscope_box .good_box h5 {
    font-size: 1rem;
    font-weight: bold;
    @include Mta{
      font-size: 0.8rem;
      white-space : nowrap; 
      padding-right: 10px;
    }
  }
  .horoscope_box .good_box span {
    font-size: 1rem;
    font-weight: bold;
    @include Mta{
      font-size: 0.8rem;
      padding-left: 10px;
    }
  }
  .horoscope_box .good_box .bar {
    width: 70%;
    border-radius: 50px;
    background: rgb(233, 233, 233);
    position: relative;
    @include Mta{
      height: 70%;
    }
  }
  .horoscope_box .good_box .bar .Full-bar {
    position: absolute;
    width: 70%;
    height: 100%;
    border-radius: 50px;
    background: #db0b15;
    top: 0;
    left: 0;
    @include Mta{
      font-size: 0.8rem;
    }
  }
  .horoscope_box .good_box .bar .Full-bar2 {
    position: absolute;
    width: 30%;
    height: 100%;
    border-radius: 50px;
    background: #db0b15;
    top: 0;
    left: 0;
  }
  .horoscope_box .good_box .bar .Full-bar3 {
    position: absolute;
    width: 15%;
    height: 100%;
    border-radius: 50px;
    background: #db0b15;
    top: 0;
    left: 0;
  }
  .Photo {
    width: 100%;
    height: 200px;
    display: flex;
    .P_Box{
      width: 100%;
      background: red;
      margin: 2%;
      cursor: pointer;
    }
    .P_Box:hover{
      filter: grayscale(1);
    }
    @include Mta{
      height: 150px;
    }
  }
  
  .Drop {
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2%;
    border-bottom: 1.5px solid gray;
    border-top: 1.5px solid gray;
    @include Mta{
      white-space : nowrap;
    }
    .btn{
      background: #db0b15;
      margin-top: 30px;
      @include Mta{
        white-space : nowrap;
        margin-right: 10px;
      }
    }
    label{
      font-size: 1rem;
    }
    .mb-3{
      width: 300px;
      margin-top: 20px;
    }
    .form-check-input[type=checkbox] {
      margin-top: 10px;
  }
}
  .Review_content {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
  }
  .Review_content .Customer {
    width: 100%;
    height: auto;
    border-bottom: 1.5px solid gray;
    display: flex;
    align-items: center;
  }
  .Review_content .Customer:nth-of-type(5){
    border-bottom: none;
  }
  .Review_content .Customer .CO {
    width: 70%;
    text-align: left;
    font-size: 1.1rem;
    margin-top: 3%;
    p{
      margin: 2%;
      font-size: 1rem;
    }
    .btn{
      margin-top: 20px;
      font-weight: bold;
    }
    .good_bad{
      height: 30px;
      margin-left: 10px;
      margin-top: 10px;
      margin-bottom: 20px;
      span{
        font-size: 1rem;
        @include sub_Txt_color;
      }
      img{
        cursor: pointer;
      }
      img:hover{
        opacity: 0.7;
      }
    }
  }
  .Review_content .Customer .CO .STarSTar {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 2%;
    h3{
      font-size: 1.2rem;
      font-weight: bold;
      margin-right: 30%;
      margin-top: 10px;
      @include Mta{
        white-space : nowrap;
        font-size: 1.1rem;
        margin-right: 10%;
      }
    }
    span{
      font-size: 1rem;
      @include sub_Txt_color;
    }
  }
  .Review_content .Customer .CO .STarSTar img {
    margin-right: 5%;
  }
  .Review_content .Customer .CO .Ph_box {
    width: 100%;
    height: auto;
    display: flex;
  }
  .Review_content .Customer .CO .Ph_box .Ph {
    width: 100%;
    height: 150px;
    margin: 2%;
  }
  .Review_content .Customer .CO .btn {
    font-size: 1rem;
    background: gainsboro;
    color: black;
  }
  .Review_content .Customer .userr {
    width: 30%;
    height: 250px;
    border-left: 1px solid gray;
    p {
      font-size: 1rem;
      margin-top: 5%;
      padding-left: 10%;
      text-align: left;
    }
  }
/*Q&A*/
.Q_Container{
  @include Margin_Bt;
  .btn{
    width: 100px;
    margin: 2%;
    float: right;
    margin-bottom: 5%;
  }
  .table-responsive-md{
    @include Mta{
      width: 100%;
    }
  }
  .btn:hover{
    opacity: 0.6;
  }
  table{
    font-size: 1.1rem;
    @include Mta{
      white-space: nowrap;
      font-size: 1rem;
    }
    thead{
      th{
        font-size: 1.2rem;
      }
      
    }
    .naaame{
      width: 50%;
    }
    img{
      margin-right: 10px;
      margin-bottom: 2px;
    }
  }
}
.Q_Container h2{
    font-size: 2.6rem;
    margin-bottom: 50px;
    font-weight: bold;
}
/*페이지번호*/
.PageNo{
  width: 100%;
  margin-top: 5%;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  .nav-item{
    padding: 1.5%;
  }
  span{
    cursor: pointer;
  }
  span:hover{
    font-weight: bold;
  }
}
.Page_co{
  color: black;
}
.Page_co:hover{
  color: #db0b15;
}
.nav-underline .nav-link.active, .nav-underline .show>.Page_co {
  color: #db0b15;
  border-bottom-color: currentcolor;
  font-weight: bold;
}
/*추천제품*/
.over{
  h2{
      font-size: 2.6rem;
    margin-bottom: 50px;
    font-weight: bold;
  }

}


