@charset "utf-8";
@import 'mixin';

.Rolling {
  margin-top: 10%;
  margin-bottom: 10%;
  h2{
    @include title_txt;
    text-align: left;
    margin-left: 6.5%;
  }
  .slide_container {
      overflow: hidden;
      @include sub_Color;
      .slide_rolling {
          display: flex;
          flex-wrap: nowrap;
      }
      .slide {
          display: flex;
          align-items: center;
          flex-wrap: nowrap;
          position: relative;
          padding: 40px 0;
          &.original {
              animation: 70s linear infinite normal none running infiniteAnimation1;
          }
          &.clone {
              animation: 70s linear infinite infiniteAnimation2;
          }
          &.stop {
              animation-play-state: paused;
          }
          li {
              margin: 0 20px;
              cursor: pointer;
              z-index: 2;
              transition: 0.3s;
              transform: scale(1);
              &:hover {
                  transform: scale(0.98);
                  &::after {
                      content: "";
                      position: absolute;
                      top: 0;
                      left: 0;
                      bottom: 0;
                      right: 0;
                      width: 100%;
                      height: 100%;
                      background-color: rgba(0, 0, 0, 0.2);
                  }
              }
              &.img_box1 {
                  width: 461px;
                  height: 640px;
                  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
                //   background: url(../img2/collection1.jpg);
              }
              &.img_box2 {
                  width: 420px;
                  height: 560px;
                  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
                //   background: url(../img2/collection2.jpg);
              }
              .slide_box {
                  width: 100%;
                  height: 100%;
              }
          }
      }
  }
}

@keyframes infiniteAnimation1 {
  0% {
      transform: translateX(0%);
  }
  50% {
      transform: translateX(-100%);
  }
  50.1% {
      transform: translateX(100%);
  }
  100% {
      transform: translateX(0%);
  }
}
@keyframes infiniteAnimation2 {
  0% {
      transform: translateX(0%);
  }
  100% {
      transform: translateX(-200%);
  }
}